const INITIAL_STATE = { summary: { credit: 0, debit: 0 } }

export default function(state = INITIAL_STATE, action) {
  switch (action.type) {
    case 'BILLING_SUMMARY_FETCHED':
    console.log(action)
      return { ...state, summary: action.payload.data }
    default:
      return state
    }
}


