import React, { Component } from 'react'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { reduxForm, Field, formValueSelector } from 'redux-form'

import { init } from './billingCycleActions'
import ItemList from './ItemList'
import LabelAndInput from '../../common/form/labelAndInput'
import Summary from './summary'

class BillingCycleForm extends Component {

  calculateSum () {
    const sum = (a, b) => a + b
    return {
      sumOfCredits: this.props.credits.map(el => +el.value || 0).reduce(sum),
      sumOfDebits: this.props.debits.map(el => +el.value || 0).reduce(sum),
    }
  }

  render () {
    const { handleSubmit, readOnly, credits, debits } = this.props
    const { sumOfCredits, sumOfDebits } = this.calculateSum()
    return (
      <form onSubmit={handleSubmit}>
        <div className='box-body'>
        <Field name='name' component={LabelAndInput} readOnly={readOnly}
          label='Nome' cols='12 4' placeholder='Informe o nome' />
        <Field name='month' component={LabelAndInput} type='number' readOnly={readOnly}
          label='Mês' cols='12 4' placeholder='Informe o mês' />
        <Field name='year' component={LabelAndInput} type='number' readOnly={readOnly}
          label='Ano' cols='12 4' placeholder='Informe o ano' />
        <Summary credit={sumOfCredits} debit={sumOfDebits} />
        <ItemList cols='12 6' list={credits} readOnly={readOnly}
          field='credits' legend='Créditos' />
        <ItemList cols='12 6' list={debits} readOnly={readOnly}
          field='debits' showStatus legend='Débitos' />
        </div>
        <div className='box-footer'>
          <button
            type='submit'
            className={`btn btn-${this.props.submitClass} pull-right`}>
            {this.props.submitLabel}
          </button>
          <button type='button' className='btn btn-default pull-right' onClick={this.props.init}> Cancelar </button>
        </div>
      </form>
    )
  }
}

BillingCycleForm = reduxForm({form: 'billingCycleForm', destroyOnUnmount: false})(BillingCycleForm)
const selector = formValueSelector('billingCycleForm')
const mapStateToProps = state => ({
  credits: selector(state, 'credits'),
  debits: selector(state, 'debits')
})
const mapDispatchToProps = dispatch => bindActionCreators({init}, dispatch)
export default connect(mapStateToProps, mapDispatchToProps)(BillingCycleForm)


